.Row {
  display: -ms-flexbox;
  display: flex;
}

.Row + .Row {
  margin-top: 1.5em;
}

.Speaker {
  font-weight: bold;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.Text {
  width: 40em;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.Text .Itallic {
  font-style: italic;
  font-size: smaller;
  margin-left: 1em;
}

.Text p {
  margin: 0;
}

.Text p + p {
  margin-top: 0.5em;
}



